@import url("https://fonts.googleapis.com/css?family=Markazi+Text:400,500,600,700");

@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,700");

html {
    font-family: 'Markazi Text', serif;
    font-size: 14px;
}

body {
    margin: 0;
}

.container {
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
}

.leyenda {
    list-style: none;
    margin: 0;
    padding: 0;
    margin: 7px 0;
    text-align: right;
}

.leyenda .label {
    display: inline;
    margin-right: 10px;
}

.leyenda li {
    margin: 0;
    padding: 0;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
}

.leyenda li span {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-right: 2px;
    vertical-align: middle;
}

.color1 {
    background-color: #80172d;
    fill: #80172d;
}

.color2 {
    background-color: #00b394;
    fill: #00b394;
}

.color3 {
    background-color: #81b1e2;
    fill: #81b1e2;
}

.color4 {
    background-color: #ef8a62;
    fill: #ef8a62;
}

.color5 {
    background-color: #386cb0;
    fill: #386cb0;
}

.color6 {
    background-color: #fee08b;
    fill: #fee08b;
}

#root {
    background-color: #dddddd;
    position: relative;
    width: 1000px;
    height: 540px;
    margin: 0 auto;
}

#wrapper {
    position: absolute;
    height: calc(100% - 85px);
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
}

#box {
    position: relative;
}

#box .item {
    font-size: 15px;
    line-height: 1.05;
    box-sizing: border-box;
    position: absolute;
    background-color: #fff;
    padding: 0.5rem;
    width: 330px;
    box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}

.hl {
    cursor: pointer;
}

/* Tooltip */
.tooltip {
    position: absolute;
    padding: 7px;
    border-radius: 3px;
    background-color: #000;
    background-color: hsla(0, 0%, 12%, 0.9);
    color: #fff;
    font-size: 16px;
    font-weight: 300;
    line-height: 1.1;
    transform: translate(-50%, 10px);
    z-index: 1000;
    pointer-events: none;
    display: none;
    width: 90%;
}

.tooltip .triangle {
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    width: 0;
    border-bottom: 5px solid #000;
    border-bottom: 5px solid hsla(0, 0%, 12%, 0.9);
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    content: " ";
    font-size: 0;
    line-height: 0;
    z-index: 1000;
}

@media only screen and (max-width: 500px) {
    #root {
        width: 100%;
        max-width: 1000px;
    }

    #wrapper {
        display: none;
    }
}
